import * as PIXI from "pixi.js";
import { Sprite } from "../loader";

interface PulseParams {
  renderer: PIXI.Renderer | PIXI.CanvasRenderer;
  stage: PIXI.Container;
  radius: number;
  x: number;
  y: number;
}

export default class Pulse {
  sprite: PIXI.Sprite;
  shadow: PIXI.Graphics;
  glow: PIXI.Graphics;
  stage: PIXI.Container;
  radius: number;
  diameter: number;
  x: number;
  y: number;
  animationId?: number;
  blurFilter: PIXI.filters.BlurFilter = new PIXI.filters.BlurFilter();

  constructor({ x, y, radius, stage }: PulseParams) {
    this.radius = radius;
    this.diameter = this.radius * 2;
    this.stage = stage;
    this.x = x;
    this.y = y;

    this.sprite = this.createSprite();
    this.shadow = this.createShadow();
    this.glow = this.createGlow();

    this.addToRenderer();
    this.rise();
  }

  createSprite = (): PIXI.Sprite => {
    const texture =
      PIXI.Loader.shared.resources[Sprite.PulseInnerShadow].texture;
    const pulse = PIXI.Sprite.from(texture);

    pulse.position.x = this.x;
    pulse.position.y = this.y;
    pulse.height = this.diameter;
    pulse.width = this.diameter;
    pulse.zIndex = 20;
    pulse.anchor.set(0.5, 0.5);

    return pulse;
  };

  createShadow = (): PIXI.Graphics => {
    const graphics = new PIXI.Graphics();
    graphics.beginFill(0xff00d0);
    graphics.lineStyle(0);
    graphics.drawCircle(0, 0, this.radius);
    graphics.filters = [this.blurFilter];
    graphics.endFill();

    graphics.position.x = this.x;
    graphics.position.y = this.y;
    graphics.height = this.diameter;
    graphics.width = this.diameter;
    graphics.zIndex = 1;

    return graphics;
  };

  createGlow = (): PIXI.Graphics => {
    const graphics = new PIXI.Graphics();
    graphics.beginFill(0xffe377, 0.07);
    graphics.lineStyle(0);
    graphics.drawCircle(0, 0, this.radius);
    graphics.endFill();

    graphics.position.x = this.x;
    graphics.position.y = this.y;
    graphics.height = this.diameter;
    graphics.width = this.diameter;
    graphics.zIndex = 2;

    return graphics;
  };

  rise = () => {
    const riseBlurFactor = 1.1;
    const riseGlowFactor = 1.03;
    const targetBlur = 24;

    this.blurFilter.blur *= riseBlurFactor;

    this.glow.scale.x *= riseGlowFactor;
    this.glow.scale.y *= riseGlowFactor;

    if (this.blurFilter.blur > targetBlur) {
      this.remove();

      if (this.animationId) {
        cancelAnimationFrame(this.animationId);
      }

      return;
    }

    this.animationId = requestAnimationFrame(this.rise);
  };

  addToRenderer = () => {
    this.stage.addChild(this.sprite);
    this.stage.addChild(this.shadow);
    this.stage.addChild(this.glow);
  };

  remove = () => {
    this.stage?.removeChild(this.sprite);
    this.stage?.removeChild(this.shadow);
    this.stage?.removeChild(this.glow);
  };
}
