import * as PIXI from "pixi.js";
import { Sprite } from "../loader";
import { random } from "games/Plinko/shared/utils/math";

const getRandomAngle = (): number => {
  return random(0, 360);
};

export const getAngles = (numberOfAngles: number) => {
  let rotations: number[] = [];

  const handle = (): number[] => {
    const newRotation = getRandomAngle();

    if (rotations.length >= numberOfAngles) {
      return rotations;
    }

    if (rotations.every((r) => Math.abs(r - newRotation) > 40)) {
      rotations.push(newRotation);
    }

    return handle();
  };

  return handle();
};

interface SparkParams {
  stage: PIXI.Container;
  x: number;
  y: number;
  angle: number;
}

export default class Spark {
  spark: PIXI.Sprite;

  stage: PIXI.Container;
  x: number;
  y: number;
  angle: number;
  animationId?: number;
  tickPrevTime: number = 0;
  distanceCovered: number = 0;
  blurFilter: PIXI.filters.BlurFilter = new PIXI.filters.BlurFilter();

  constructor({ x, y, angle, stage }: SparkParams) {
    this.stage = stage;
    this.x = x;
    this.y = y;
    this.angle = angle;

    this.spark = this.createSpark();

    this.addToRenderer();
    this.tick(0);
  }

  createSpark = (): PIXI.Sprite => {
    const texture = PIXI.Loader.shared.resources[Sprite.Spark].texture;
    const spark = PIXI.Sprite.from(texture);

    spark.position.x = this.x;
    spark.position.y = this.y;
    spark.angle = this.angle;
    spark.height = 10;
    spark.width = 3;
    spark.zIndex = 12;
    spark.anchor.set(0.5, 0.5);

    return spark;
  };

  tick = (time?: number) => {
    const targetDistance = 40;

    if (time && !this.tickPrevTime) {
      this.tickPrevTime = time;
    }

    if (time && this.tickPrevTime) {
      const distance = Math.max((time - this.tickPrevTime) / 10, 0);

      this.spark.x =
        this.spark.x + distance * Math.cos(((this.angle - 90) * Math.PI) / 180);
      this.spark.y =
        this.spark.y + distance * Math.sin(((this.angle - 90) * Math.PI) / 180);

      this.tickPrevTime = time;
      this.distanceCovered = this.distanceCovered + distance;

      this.spark.alpha = Math.max(0, 1 - this.distanceCovered / targetDistance);

      if (this.distanceCovered > targetDistance) {
        this.remove();

        if (this.animationId) {
          cancelAnimationFrame(this.animationId);
        }
        return;
      }
    }

    this.animationId = requestAnimationFrame(this.tick);
  };

  addToRenderer = () => {
    this.stage.addChild(this.spark);
  };

  remove = () => {
    this.stage?.removeChild(this.spark);
  };
}
