import * as PIXI from "pixi.js";
import { Sprite } from "../loader";
import { BASKET } from "../shared/constants/bodies";

interface BasketParams {
  stage: PIXI.Container;
  value: number;
  height: number;
  width: number;
  x: number;
  y: number;
}

export default class Basket {
  sprite: PIXI.Sprite;
  text: PIXI.Text;
  stage: PIXI.Container;
  value: number;
  height: number;
  width: number;
  x: number;
  y: number;
  isShakeDown: boolean = false;
  targetYPosition: number = 0;
  step: number = 0;
  animationId?: number;

  constructor({ x, y, height, width, stage, value }: BasketParams) {
    this.stage = stage;
    this.height = height;
    this.width = width;
    this.x = x;
    this.y = y;
    this.value = value;

    this.sprite = this.createSprite();
    this.text = this.createText();

    this.addToRenderer();
  }

  getBasketTexture = () => {
    if (this.value >= 2) {
      return Sprite.Basket_PastelGreen;
    } else if (this.value >= 1.4) {
      return Sprite.Basket_YellowGreen;
    } else if (this.value >= 1.2) {
      return Sprite.Basket_Dolly;
    } else if (this.value >= 1) {
      return Sprite.Basket_Macaroni;
    }

    return Sprite.Basket_VividTangerine;
  };

  createSprite = (): PIXI.Sprite => {
    let texture = PIXI.Loader.shared.resources[this.getBasketTexture()].texture;
    const basket = PIXI.Sprite.from(texture);

    basket.position.x = this.x;
    basket.position.y = this.y;
    basket.height = this.height;
    basket.width = this.width;

    return basket;
  };

  createText = (): PIXI.Text => {
    const text = new PIXI.Text(`${this.value}x`, {
      fontFamily: BASKET.FONT_FAMILY,
      fontSize: 13,
      fontWeight: BASKET.FONT_WEIGHT,
      fill: BASKET.FONT_COLOR,
    });

    text.style.letterSpacing = 1.04;
    text.position.x = this.x + this.width / 2;
    text.position.y = this.y + this.height / 2;

    text.anchor.set(0.5, 0.5);

    return text;
  };

  shake = () => {
    this.isShakeDown = true;
    this.targetYPosition = this.sprite.y + 15;
    this.step = 2;

    requestAnimationFrame(this.shakeAnimation);
  };

  shakeAnimation = () => {
    if (this.isShakeDown && this.sprite.y >= this.targetYPosition) {
      this.isShakeDown = false;
    }

    this.sprite.position.y = this.isShakeDown
      ? this.sprite.y + this.step
      : this.sprite.y - this.step;
    this.text.position.y = this.isShakeDown
      ? this.text.y + this.step
      : this.text.y - this.step;

    if (!this.isShakeDown && this.sprite.y <= this.y) {
      this.sprite.y = this.y;
      this.text.y = this.y + this.height / 2;

      if (this.animationId) {
        cancelAnimationFrame(this.animationId);
      }

      return;
    }

    this.animationId = requestAnimationFrame(this.shakeAnimation);
  };

  addToRenderer = () => {
    this.stage.addChild(this.sprite);
    this.stage.addChild(this.text);
  };
}
