import * as PIXI from "pixi.js";

import Ball from "./shared/sprites/Ball.png";
import Peg from "./shared/sprites/Peg.png";
import Peg_Dark from "./shared/sprites/Peg_Dark.png";
import Rectangle from "./shared/sprites/Rectangle.png";
import Pulse from "./shared/sprites/Pulse.png";
import PegShadow from "./shared/sprites/PegShadow.png";
import PulseInnerShadow from "./shared/sprites/PulseInnerShadow.png";
import Spark from "./shared/sprites/Spark.png";

import Basket_Dolly from "./shared/sprites/Basket_Dolly.png";
import Basket_Macaroni from "./shared/sprites/Basket_Macaroni.png";
import Basket_PastelGreen from "./shared/sprites/Basket_PastelGreen.png";
import Basket_VividTangerine from "./shared/sprites/Basket_VividTangerine.png";
import Basket_YellowGreen from "./shared/sprites/Basket_YellowGreen.png";

export enum Sprite {
  Ball = "Ball",
  Peg = "Peg",
  Peg_Dark = "Peg_Dark",
  Rectangle = "Rectangle",
  Pulse = "Pulse",
  PegShadow = "PegShadow",
  PulseInnerShadow = "PulseInnerShadow",
  Spark = "Spark",

  Basket_PastelGreen = "Basket_PastelGreen",
  Basket_YellowGreen = "Basket_YellowGreen",
  Basket_Dolly = "Basket_Dolly",
  Basket_Macaroni = "Basket_Macaroni",
  Basket_VividTangerine = "Basket_VividTangerine",
}

const sprites: {
  name: Sprite;
  url: string;
}[] = [
  {
    name: Sprite.Ball,
    url: Ball,
  },
  {
    name: Sprite.Peg,
    url: Peg,
  },
  {
    name: Sprite.Peg_Dark,
    url: Peg_Dark,
  },
  {
    name: Sprite.Rectangle,
    url: Rectangle,
  },
  {
    name: Sprite.Pulse,
    url: Pulse,
  },
  {
    name: Sprite.PegShadow,
    url: PegShadow,
  },
  {
    name: Sprite.PulseInnerShadow,
    url: PulseInnerShadow,
  },
  {
    name: Sprite.Spark,
    url: Spark,
  },
  {
    name: Sprite.Basket_Dolly,
    url: Basket_Dolly,
  },
  {
    name: Sprite.Basket_Macaroni,
    url: Basket_Macaroni,
  },
  {
    name: Sprite.Basket_PastelGreen,
    url: Basket_PastelGreen,
  },
  {
    name: Sprite.Basket_VividTangerine,
    url: Basket_VividTangerine,
  },
  {
    name: Sprite.Basket_YellowGreen,
    url: Basket_YellowGreen,
  },
];

const PIXILoader = (callback?: () => any) => {
  PIXI.Loader.shared.load((loader, resources) => {
    const resourcesLength = Object.keys(resources).length;
    loader.add(!resourcesLength ? sprites : []).load(callback);
  });
};

export default PIXILoader;
