import { Dimensions } from "../engine";

export interface Props {
  dimensions: Dimensions;
  length: number;
  disabled: {
    baskets: boolean;
    fps: boolean;
  };
}

export interface Workspace {
  rows: number;
  cols: number;
  width: number;
  height: number;
  verticalMargin: number;
  horizontalMargin: number;
  verticalWorkSpaceMargin: number;
  horizontalWorkSpaceMargin: number;
  rowSpacing: number;
  colSpacing: number;
  throwFromX: number;
  throwToX: number;
  ballDiameter: number;
  pegDiameter: number;
}

const prepareVariables = ({
  dimensions: { height, width },
  length,
  disabled,
}: Props): Workspace => {
  const rows = length - 1;
  const cols = length;

  let workspaceWidth = width;
  let workspaceHeight = height;

  let verticalMargin = 50;
  const horizontalMargin = 0;

  let spacingRows = rows + 2;

  if (disabled.baskets) {
    spacingRows = rows + 1;
  }

  const ballDiameter = workspaceWidth / length / 3;
  const pegDiameter = workspaceWidth / length / 4.5;

  const rowSpacing = (workspaceHeight - verticalMargin) / spacingRows;
  const colSpacing = (workspaceWidth - pegDiameter) / cols;

  const verticalWorkSpaceMargin = 0;
  const horizontalWorkSpaceMargin = 0;

  const throwFromX =
    pegDiameter / 2 +
    horizontalWorkSpaceMargin +
    horizontalMargin +
    (colSpacing * (cols - 2)) / 2 +
    ballDiameter;

  const throwToX = throwFromX + 2 * colSpacing - 2 * ballDiameter;

  return {
    rows,
    cols,
    width: workspaceWidth,
    height: workspaceHeight,
    verticalMargin,
    horizontalMargin,
    verticalWorkSpaceMargin,
    horizontalWorkSpaceMargin,
    rowSpacing,
    colSpacing,
    throwFromX,
    throwToX,
    ballDiameter,
    pegDiameter,
  };
};

export default prepareVariables;
