import { Workspace } from "./prepareVariables";
import BallDropper from "../bodies/BallDropper";

interface Props {
  workSpace: Workspace;
  stage: PIXI.Container;
}

const createBallDropper = ({ workSpace, stage }: Props) => {
  const { throwFromX, throwToX, rowSpacing, ballDiameter } = workSpace;

  const ballDropperRatio = 104 / 38;

  const width = throwToX - throwFromX + ballDiameter;
  const height = width / ballDropperRatio;

  new BallDropper({
    x: throwFromX - ballDiameter / 2,
    y: rowSpacing - height,
    height,
    width,
    stage,
  });
};

export default createBallDropper;
