import * as PIXI from "pixi.js";
import { Bodies, Body } from "matter-js";

import { BALL } from "../shared/constants";
import GameObject, { GameObjectParams } from "./GameObject";
import { Sprite } from "../loader";

interface BallParams extends GameObjectParams {
  predictedResult?: number;
  diameter: number;
}

export interface BallBodyWithParentObject extends Body {
  parentObject?: Ball;
  predictedResult?: number;
  startedPosition?: {
    x: number;
    y: number;
  };
}

export default class Ball extends GameObject {
  type: string = "ball";
  restitution: number = BALL.RESTITUTION;
  friction: number = BALL.FRICTION;
  radius: number;
  density: number = BALL.DENSITY;
  isStatic: boolean = false;
  body: BallBodyWithParentObject;
  sprite?: PIXI.DisplayObject;

  shrinking = false;

  constructor({ id, x, y, predictedResult, diameter, ...params }: BallParams) {
    super({
      ...params,
      id,
      x,
      y,
    });

    this.radius = diameter / 2;

    this.body = this.createPhysics();
    this.body.parent = this.body;
    this.body.parentObject = this;
    this.body.predictedResult = predictedResult;
    this.body.startedPosition = {
      x,
      y,
    };

    if (!this.withoutRenderer) {
      this.sprite = this.createSprite();
    }
  }

  createPhysics = (): Body => {
    const options = {
      restitution: this.restitution,
      friction: this.friction,
    };

    const preparedBody = Bodies.circle(this.x, this.y, this.radius, options);
    Body.setDensity(preparedBody, this.density);

    preparedBody.label = this.type;
    preparedBody.position.x = this.x;
    preparedBody.position.y = this.y;
    preparedBody.isStatic = this.isStatic;

    return preparedBody;
  };

  createSprite = (): PIXI.Sprite => {
    const texture = PIXI.Loader.shared.resources[Sprite.Ball].texture;

    const ball = PIXI.Sprite.from(texture);

    const diameter = this.radius * 2;

    ball.position.x = this.x;
    ball.position.y = this.y;
    ball.height = diameter;
    ball.width = diameter;
    ball.zIndex = 30;
    ball.anchor.set(0.5, 0.5);

    return ball;
  };

  remove = (callback: () => void) => {
    if (this.shrinking) {
      return;
    }
    this.shrinking = true;

    callback();
  };
}
