import { Workspace } from "./prepareVariables";
import Basket from "../bodies/Basket";

interface Props {
  multipliers: number[];
  workSpace: Workspace;
  stage: PIXI.Container;
}

const createBaskets = ({ multipliers, workSpace, stage }: Props) => {
  const {
    height,
    horizontalMargin,
    horizontalWorkSpaceMargin,
    rowSpacing,
    colSpacing,
    pegDiameter,
    verticalMargin,
  } = workSpace;

  const basketMargin = 4;

  const basketHeight = rowSpacing / 2 + basketMargin;
  const basketWidth = colSpacing - basketMargin * 2;

  return multipliers.map((multiplier, key) => {
    const y = height - basketHeight - verticalMargin / 2 - 15;

    const x =
      horizontalWorkSpaceMargin +
      horizontalMargin +
      pegDiameter / 2 +
      key * colSpacing +
      basketMargin;

    const basket = new Basket({
      x,
      y,
      height: basketHeight,
      width: basketWidth,
      stage,
      value: multiplier,
    });

    return basket;
  });
};

export default createBaskets;
