import * as PIXI from "pixi.js-legacy";

import {
  Engine,
  Events,
  IEventCollision,
  World,
  Vector,
  Render,
} from "matter-js";

import { track } from "utils";
import { isDevelop } from "utils/isDevelopment";

import { FPS, CANVAS, GRAVITATION } from "./shared/constants";
import FpsMeter from "./shared/utils/fps-meter";
import updateSize from "./shared/utils/updateSize";

import Loader from "./loader";

import Peg from "./bodies/Peg";
import Ball, { BallBodyWithParentObject } from "./bodies/Ball";
import Pulse from "./bodies/Pulse";
import Basket from "./bodies/Basket";
import Spark, { getAngles } from "./bodies/Spark";

import prepareVariables from "./components/prepareVariables";
import createWalls from "./components/createWalls";
import createPegs from "./components/createPegs";
import createBaskets from "./components/createBaskets";
import createBallDropper from "./components/createBallDropper";

import resultTables from "./generated/resultTables.json";
import { threadId } from "worker_threads";

const isDevelopment = isDevelop();

if (isDevelopment) {
  console.log(" ~resultTables list: ", resultTables);
}
const settings = {
  rowAmount: 0,

  get amount() {
    return this.rowAmount;
  },
  set amount(value: number) {
    this.rowAmount = value;
  },
};

const forResult = (cell: number = 0) => {
  //@ts-ignore
  resultTables[settings.amount][cell].forEach((value, i, src) => {
    //@ts-ignore
    window.testPlinko(cell, i);
  });

  return settings.amount;
};

//@ts-ignore
window.testPlinkoResults = (cell: number) => forResult(cell);

let fpsMeter: FpsMeter;

export const defaultOptions = {
  transparent: true,
  antialias: false,
  resolution: window.innerWidth > 500 ? 1.5 : 0.8,
  autoDensity: true,
};

export interface Dimensions {
  width: number;
  height: number;
}

export interface Map<T> {
  [key: string]: T;
}

export interface EngineProps {
  container: HTMLElement;
  soundEffects: {
    [key: string]: () => void;
  };
}

interface BallWithCallback extends Ball {
  callback?: () => void;
}

export default class ClientEngine {
  container: HTMLElement;
  soundEffects: {
    [key: string]: () => void;
  };
  dimensions: Dimensions = {
    width: CANVAS.WIDTH,
    height: CANVAS.HEIGHT,
  };

  renderer: PIXI.Renderer | PIXI.CanvasRenderer;
  loader = PIXI.Loader.shared;
  stage = new PIXI.Container();
  ticker = new PIXI.Ticker();
  fpsMax = FPS;

  engine = Engine.create(); // { timing: { timestamp: 0, timeScale: 2 } }

  balls: Map<BallWithCallback> = {};
  trajectories: Map<Vector[]> = {};
  pegs: Peg[] = [];

  baskets: Basket[] = [];
  disabled: {
    baskets: boolean;
    fps: boolean;
  } = {
    baskets: false,
    fps: false,
  };
  multipliers: number[] = [];
  lastBallId = 0;
  numberOfMultipliers = 0;
  resourcesLoaded = false;
  rendered = false;
  lastTimestamp = 0;
  initCallback?: () => void;
  animationId?: number;

  workSpace = {
    rows: 0,
    cols: 0,
    height: 0,
    width: 0,
    verticalMargin: 0,
    horizontalMargin: 0,
    verticalWorkSpaceMargin: 0,
    horizontalWorkSpaceMargin: 0,
    rowSpacing: 0,
    colSpacing: 0,
    throwFromX: 0,
    throwToX: 0,
    ballDiameter: 0,
    pegDiameter: 0,
  };
  onGroundCollisionCallback?: (column: number) => void;
  delta = 0;
  lastFrameTime = 0;
  renderMatter?: Render;

  constructor({ container, soundEffects }: EngineProps) {
    this.container = container;
    this.soundEffects = soundEffects;

    this.stage.sortableChildren = true;

    this.renderer = PIXI.autoDetectRenderer({
      ...defaultOptions,
      ...this.dimensions,
    });

    Loader(() => {
      this.resourcesLoaded = true;

      if (this.initCallback) {
        this.initCallback();
        this.initCallback = undefined;
      }
    });

    this.engine.world.gravity.y = GRAVITATION;
  }

  private setupFPSMeter = () => {
    const fpsMeterItem = document.createElement("div");
    fpsMeterItem.classList.add("fps");

    this.container.appendChild(fpsMeterItem);
    fpsMeter = new FpsMeter(() => {
      fpsMeterItem.innerHTML = `FPS: ${
        fpsMeter.getFrameRate().toFixed(2).toString() || "NaN"
      }`;
    });

    setInterval(() => fpsMeter.updateTime(), 1000.0 / this.fpsMax);
  };

  private setup = () => {
    this.clearEnvironment();

    if (isDevelopment && !this.disabled.fps) {
      this.setupFPSMeter();
    }

    this.createEnvironment();

    if (this.renderMatter) {
      Render.run(this.renderMatter);
    }

    if (!this.rendered) {
      this.render();
    }
  };

  init = (
    value: number[],
    callback?: () => void,
    disabled?: {
      baskets?: boolean;
      fps?: boolean;
    },
  ) => {
    if (!value) {
      return;
    }

    this.multipliers = value;
    this.trajectories = (resultTables as Map<Map<Vector[]>>)[value.length];
    settings.amount = value.length;

    const handleSetup = () => {
      this.setup();

      if (callback) {
        callback();
      }
    };
    if (disabled) {
      if (disabled.baskets !== undefined) {
        this.disabled.baskets = disabled.baskets;
      }
      if (disabled.fps !== undefined) {
        this.disabled.fps = disabled.fps;
      }
    }

    if (this.resourcesLoaded) {
      return handleSetup();
    }

    this.initCallback = handleSetup;
  };

  clearEnvironment = () => {
    if (this.animationId) {
      this.rendered = false;
      cancelAnimationFrame(this.animationId);
    }

    World.clear(this.engine.world, false);
    Engine.clear(this.engine);
    this.removePhysicsEvents();
    let fpsMeter = this.container.getElementsByClassName("fps");
    for (let i = fpsMeter.length - 1; i >= 0; --i) {
      fpsMeter[i].remove();
    }

    this.renderer.view.remove();

    for (var i = this.stage.children.length - 1; i >= 0; i--) {
      this.stage?.removeChild(this.stage.children[i]);
    }

    this.pegs = [];
    this.balls = {};
  };

  private createEnvironment = () => {
    this.container.appendChild(this.renderer.view);
    updateSize(this.container);

    this.workSpace = prepareVariables({
      dimensions: this.dimensions,
      length: this.multipliers.length,
      disabled: this.disabled,
    });
    this.pegs = createPegs({
      workSpace: this.workSpace,
      engine: this.engine,
      stage: this.stage,
      renderer: this.renderer,
    });
    createWalls({
      workSpace: this.workSpace,
      dimensions: this.dimensions,
      engine: this.engine,
    });
    createBallDropper({
      workSpace: this.workSpace,
      stage: this.stage,
    });
    if (!this.disabled.baskets) {
      this.baskets = createBaskets({
        multipliers: this.multipliers,
        workSpace: this.workSpace,
        stage: this.stage,
      });
    }

    this.registerPhysicsEvents();
  };

  onGroundCollision = (callback: (column: number) => void) => {
    this.onGroundCollisionCallback = callback;
  };

  private trigger = (column: number) => {
    if (!this.onGroundCollisionCallback) {
      return;
    }

    this.onGroundCollisionCallback(column);
  };

  private onCollisionStart = ({ pairs }: IEventCollision<Engine>) => {
    const {
      horizontalMargin,
      horizontalWorkSpaceMargin,
      colSpacing,
      pegDiameter,
    } = this.workSpace;

    for (let i = 0; i < pairs.length; i++) {
      const pair = pairs[i];
      const { bodyA, bodyB } = pair;
      if (bodyA.label !== "ground") {
        this.soundEffects.hashDice();
      } else {
        this.soundEffects.end();
      }

      if (bodyA.label === "ball" && bodyB.label === "ball") {
        pair.isActive = false;
      }

      if (bodyA.label === "peg" && bodyB.label === "ball") {
        const angles = getAngles(Math.min(5, Math.floor(bodyB.speed / 2.5)));
        angles.forEach(
          (angle) =>
            new Spark({
              x: bodyA.position.x,
              y: bodyA.position.y,
              stage: this.stage,
              angle,
            }),
        );
        new Pulse({
          renderer: this.renderer,
          stage: this.stage,
          radius: pegDiameter / 2,
          x: bodyA.position.x,
          y: bodyA.position.y,
        });
      }

      if (bodyA.label === "ground" && bodyB.label === "ball") {
        // remove the ball after ground collision
        const ball = (bodyB as BallBodyWithParentObject)?.parentObject;
        if (!ball) {
          return;
        }
        const { x } = bodyB.position;

        const column = Math.floor(
          (x - horizontalWorkSpaceMargin - horizontalMargin - pegDiameter) /
            colSpacing,
        );

        if (ball?.body.predictedResult !== column) {
          const errorData = {
            body: bodyB,
            waitingCell: ball?.body.predictedResult,
            resultCell: column,
            startedPosition: ball?.body?.startedPosition,
          };

          track()?.errors(
            `
              plinko cell error:
              waitingCell: ${ball?.body.predictedResult},
              resultCell: ${column},
              startedPosition: {
                x: ${
                  ball?.body?.startedPosition
                    ? ball.body.startedPosition.x
                    : "undefined"
                };
                y: ${
                  ball?.body?.startedPosition
                    ? ball.body.startedPosition.y
                    : "undefined"
                } }
            `,
          );

          if (isDevelopment) {
            console.log(
              " --------------- cell error:",
              errorData,
              ball?.body?.startedPosition?.x,
            );
          }
        }

        this.trigger(ball?.body.predictedResult || column);

        this.baskets[column].shake();

        const currentBall = this.balls[ball.id];

        if (currentBall?.callback) {
          currentBall.callback();
        }

        ball.remove(() => {
          World.remove(this.engine.world, ball.body);
          if (currentBall?.sprite) {
            this.stage?.removeChild(currentBall.sprite);
          }
          delete this.balls[ball.id];
        });
      }
    }
  };

  private registerPhysicsEvents = () => {
    Events.on(this.engine, "collisionStart", this.onCollisionStart);
  };

  private removePhysicsEvents = () => {
    Events.off(this.engine, "collisionStart", this.onCollisionStart);
  };

  playBet = (
    predictedResult: number,
    callback?: () => void,
    tVAlue?: number,
  ) => {
    this.soundEffects.start();

    const { ballDiameter } = this.workSpace;

    const resultTrajectoriesList = this.trajectories[predictedResult];
    const trajectoriesValue =
      tVAlue || tVAlue === 0
        ? tVAlue
        : Math.floor(Math.random() * resultTrajectoriesList.length);

    const actualTrajectories = resultTrajectoriesList[trajectoriesValue];
    const { x, y } = actualTrajectories;

    /*
    console.log(
      `
      cell: ${predictedResult},
      tValue: ${trajectoriesValue},
      list:
    `,
      resultTrajectoriesList,
    );
  */

    const id = this.lastBallId++ % 255;

    let ball = new Ball({
      id,
      x,
      y,
      predictedResult,
      diameter: ballDiameter,
    });

    ball.init(this.engine.world, this.stage);

    let ballId = String(ball.id);

    this.balls[ballId] = ball;
    this.balls[ballId].callback = callback;
  };

  private animate = () => {
    for (const id in this.balls) {
      if (Object.prototype.hasOwnProperty.call(this.balls, id)) {
        const ball = this.balls[id];

        if (ball.sprite) {
          ball.sprite.position.x = ball.body.position.x;
          ball.sprite.position.y = ball.body.position.y;
          ball.sprite.angle = ball.body.angle * (180 / Math.PI);
        }

        this.balls[id] = ball;
      }
    }
  };

  private render = () => {
    this.rendered = true;

    if (isDevelopment && !this.disabled.fps) {
      fpsMeter.tick();
    }
    if (Object.keys(this.balls).length) {
      Engine.update(this.engine);
    }

    this.animate();
    this.renderer.render(this.stage);
    this.animationId = requestAnimationFrame(this.render);
  };
}
