import * as PIXI from "pixi.js";
import { Bodies, Body } from "matter-js";
import { Sprite } from "../loader";

import { PEG } from "../shared/constants/bodies";
import GameObject, { GameObjectParams } from "./GameObject";

interface PegParams extends GameObjectParams {
  diameter: number;
}

export default class Peg extends GameObject {
  type: string = "peg";
  restitution: number = PEG.RESTITUTION;
  friction: number = PEG.FRICTION;
  radius: number;
  density: number = PEG.DENSITY;
  isStatic: boolean = true;
  sprite?: PIXI.DisplayObject;

  constructor({ id, x, y, diameter, ...params }: PegParams) {
    super({
      ...params,
      id,
      x,
      y,
    });

    this.radius = diameter / 2;

    this.body = this.createPhysics();
    this.body.parent = this.body;

    if (!this.withoutRenderer) {
      this.sprite = this.createSprite();
    }
  }

  createPhysics = (): Body => {
    const options = {
      restitution: this.restitution,
      friction: this.friction,
    };

    const preparedBody = Bodies.circle(this.x, this.y, this.radius, options);
    Body.setDensity(preparedBody, this.density);

    preparedBody.label = this.type;
    preparedBody.position.x = this.x;
    preparedBody.position.y = this.y;
    preparedBody.isStatic = this.isStatic;

    return preparedBody;
  };

  createSprite = (): PIXI.Sprite => {
    const sprite =
      localStorage.getItem("color-scheme") === "light"
        ? Sprite.Peg_Dark
        : Sprite.Peg;

    const texture = PIXI.Loader.shared.resources[sprite].texture;

    const peg = PIXI.Sprite.from(texture);

    const diameter = this.radius * 2;

    peg.position.x = this.x;
    peg.position.y = this.y;
    peg.height = diameter;
    peg.width = diameter;
    peg.anchor.set(0.5, 0.5);
    peg.zIndex = 10;

    return peg;
  };
}
