import { CANVAS } from "../constants";

export function handleUpdateSize(container: HTMLElement) {
  const width = container.offsetWidth;
  const height = container.offsetHeight;

  const ration = CANVAS.HEIGHT / CANVAS.WIDTH;

  let proportionalWidth = width;
  let proportionalHeight = width * ration;

  // if (height / ration < width) {
  //   proportionalWidth = height / ration;
  //   proportionalHeight = height;
  // }// poissibly not need

  const canvas = container.getElementsByTagName("canvas")[0];

  if (!canvas) {
    return;
  }

  canvas.setAttribute(
    "style",
    `width:${proportionalWidth}px; height:${proportionalHeight}px;`,
  );
}

const updateSize = (container: HTMLElement) => {
  handleUpdateSize(container);
  window.onresize = () => handleUpdateSize(container);
};

export default updateSize;
