import * as PIXI from "pixi.js";
import { Engine } from "matter-js";

import { Workspace } from "./prepareVariables";
import Peg from "../bodies/Peg";

interface Props {
  workSpace: Workspace;
  withoutRenderer?: boolean;
  engine: Engine;
  stage?: PIXI.Container;
  renderer?: PIXI.Renderer | PIXI.CanvasRenderer;
}

const createPegs = ({
  workSpace,
  engine,
  stage,
  withoutRenderer = false,
  renderer,
}: Props): Peg[] => {
  const {
    rows,
    cols,
    horizontalMargin,
    verticalWorkSpaceMargin,
    horizontalWorkSpaceMargin,
    rowSpacing,
    colSpacing,
    pegDiameter,
  } = workSpace;

  let id = 0;
  const pegs = [];

  for (let row = 0; row < rows; row++) {
    const reversedIndex = rows - row;

    for (let col = 0; col <= cols - reversedIndex + 1; col++) {
      const x =
        horizontalWorkSpaceMargin +
        horizontalMargin +
        (colSpacing * (reversedIndex - 1)) / 2 + // leave extra horizontal space for staggered display
        pegDiameter / 2 + // leave extra horizontal space for staggered display
        col * colSpacing; // horizontal space between peg

      const y =
        verticalWorkSpaceMargin + // leave extra space at top of frame to drop balls
        // verticalMargin + // leave extra space at top of frame to drop balls
        (row + 2) * rowSpacing; // vertical space between peg

      const peg = new Peg({
        id,
        x,
        y,
        withoutRenderer,
        diameter: pegDiameter,
      });
      pegs[id] = peg;

      peg.addToEngine(engine.world);

      if (!withoutRenderer && stage) {
        peg.addToRenderer(stage);
      }

      id++;
    }
  }

  return pegs;
};

export default createPegs;
