import * as PIXI from "pixi.js";
import { Sprite } from "../loader";

interface BallDropperParams {
  stage: PIXI.Container;
  height: number;
  width: number;
  x: number;
  y: number;
}

export default class BallDropper {
  sprite: PIXI.Sprite;
  stage: PIXI.Container;
  height: number;
  width: number;
  x: number;
  y: number;
  isShakeDown: boolean = false;
  targetYPosition: number = 0;
  step: number = 0;
  animationId?: number;

  constructor({ x, y, height, width, stage }: BallDropperParams) {
    this.stage = stage;
    this.height = height;
    this.width = width;
    this.x = x;
    this.y = y;

    this.sprite = this.createSprite();
    this.addToRenderer();
  }

  createSprite = (): PIXI.Sprite => {
    let texture = PIXI.Loader.shared.resources[Sprite.Rectangle].texture;
    const ballDropper = PIXI.Sprite.from(texture);

    ballDropper.position.x = this.x;
    ballDropper.position.y = this.y;
    ballDropper.height = this.height;
    ballDropper.width = this.width;

    return ballDropper;
  };

  addToRenderer = () => {
    this.stage.addChild(this.sprite);
  };
}
